.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* custom CSS */
/* this removes the whitespace area present at the top of the app */
* {
  margin: 0;
  padding: 0;
}

#root {
  padding: 1%;
  min-height: 100vh;
  max-height: 100vh;
  text-align: center;
  background-image: url(./components/tolga.jpg);
  background-size: cover;
  color: #fff;
}

a {
  color: #fff;
}

.photocredit {
  font-size: .9em;
}

.body {
  min-height: 48vh;
  margin: 4% 15%;
}

/* button styles  */
.startButton {
  margin-top: 15%;
  background: none;
  color: #fff;
  padding: 1% 5%;
  transition-duration: 0.5s;
  border: none;
}

.startButton:hover {
  color: #000;
  background-color: #fff;
}

/* css animation  */

.circle {
  border-radius: 50%;
  /* border: 2px solid #fff; */
  width: 5em;
  height: 5em;
  margin: 20% auto;
  background:none;
  animation: breathe 12s infinite;
}

@keyframes breathe {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(3);
  }
  to {
    transform: scale(1);
  }
}

/* responsive css  TO DO */ 
@media screen and (min-width: 800px) {
  .circle {
    margin: 5% auto;
  }
  .startButton {
    margin-top: 20%;
  }
}